/* Split the screen in half */
.split {
  height: 90%;
  position: fixed;
  z-index: 1;
  top: 0;
  overflow-x: hidden;
  padding-top: 20px;
  padding-bottom: 20px;
}

/* Control the left side */
.left {
  left: 0;
  width: 65%;
}

/* Control the right side */
.right {
  right: 0;
    width: 35%;
}

/* Warning Divs */
.no_break {
  display:inline;
}

.warning_ok {
  background-color: lightgreen;
  margin: 0 5px 0 5px;
}

.warning_bad {
  background-color: #f1807e;
  margin: 0 5px 0 5px;
}
